import React from 'react'
import { Card, TextField, CardMedia, CardContent, Button, Typography, CardActions, Box, Grid } from '@mui/material'
import DownloadIcon from '@mui/icons-material/Download';
import * as urls from '../../BackendUrls';


const StudentProfile = (props) => {
    let semesters = props.studentData.semesters.map(semester => {
        return (
            <Card key={semester.semester_no} sx={{mb:2}}>
                <CardMedia
                    sx={{ mt:2 }}
                    component="img"
                    image={urls.baseUrl + semester.tabulation_thumb_img}
                    title="thumbnail"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                    {semester.semester_name}
                    </Typography>
                </CardContent>
                <CardActions sx={{justifyContent: 'center', mb:2}}>
                    <Button target="_blank" href={urls.baseUrl + semester.tabulation_url}  variant='outlined' startIcon={<DownloadIcon />}>Download Tabulation</Button>
                </CardActions>
            </Card>
        )
    })
    const [fileUrls, setFileUrls] = React.useState({testimonial:null, coursemedium: null, appearedCert:null});
    const handleInputChange = event => {
        let value = event.target.value;
        if (value.length > 0) {
            setFileUrls({
                testimonial: urls.baseUrl + props.studentData.testimonial_url + "?ref=" + value,
                coursemedium: urls.baseUrl + props.studentData.coursemedium_url + "?ref=" + value,
                appearedCert: urls.baseUrl + props.studentData.appearedCert_url + "?ref=" + value,
            })
        } else {
            setFileUrls({testimonial:null, coursemedium: null, appearedCert:null})
        }
    }
    return (
        <Box className="mt-5 mb-5">
            <Grid container spacing={4}>
                <Grid item xs={12} md={7} >
                    <div className="mb-5 bg-light shadow-sm py-3 rounded">
                        <div className="text-center" style={{paddingTop: '2rem'}}>
                            <img src={ urls.baseUrl + props.studentData.profile_picture_url } alt="avatar"
                                className="rounded-circle img-fluid" style={{ width: '150px' }} />
                            <h5 className="my-3">{props.studentData.name}</h5>
                            <p className="text-muted mb-1">{props.studentData.registration}</p>
                            <p className="text-muted mb-1">Session: {props.studentData.session}</p>
                            <Typography gutterBottom variant="body1" component="div">
                                Department of {props.studentData.dept}
                            </Typography>
                        </div>
                    </div>
                    <TextField onChange={(e) => handleInputChange(e)} fullWidth className='mb-3' id="outlined-basic" label="Ref. Text" variant="outlined" />
                    <Card className="py-3" sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center', mb: 2}}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center'}}>
                            <Typography variant="h5" component="div" sx={{textAlign: {xs: 'center'}}}>
                                Testimonial
                            </Typography>
                            <CardActions>
                                <Button className="ms-3" target="_blank" href={fileUrls.testimonial} disabled={fileUrls.testimonial ? false : true}  variant='contained' startIcon={<DownloadIcon />}>Download</Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                    <Card className="py-3" sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center', mb: 2}}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center'}}>
                            <Typography variant="h5" component="div" sx={{textAlign: {xs: 'center'}}}>
                                Course Medium Certificate
                            </Typography>
                            <CardActions>
                                <Button className="ms-3" target="_blank" href={fileUrls.coursemedium} disabled={fileUrls.coursemedium ? false : true}  variant='contained' startIcon={<DownloadIcon />}>Download</Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                    <Card className="py-3" sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center', mb: 2}}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent:"center", alignItems: 'center'}}>
                            <Typography variant="h5" component="div" sx={{textAlign: {xs: 'center'}}}>
                                Appeared Certificate
                            </Typography>
                            <CardActions>
                                <Button className="ms-3" target="_blank" href={fileUrls.appearedCert} disabled={fileUrls.appearedCert ? false : true}  variant='contained' startIcon={<DownloadIcon />}>Download</Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12} md={5}>
                    {semesters}
                </Grid>
            </Grid>
        </Box>
    )
}

export default StudentProfile